@import url(https://fonts.googleapis.com/css2?family=Alegreya:wght@400;500;700&family=Inter:wght@400;500;700&display=swap);
.navbar-background {
  background-color: var(--accent-light);
  border-radius: 0 0 20px 20px;
  padding: 20px 0;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999;
  position: relative
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.nav-menu {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-gap: 30px;
}

.nav-links {
  text-decoration: none;
  z-index: 1;
}

.nav-links:hover {
  text-decoration: underline;
}

ul{
  list-style: inside;
  padding-left: 0;
}

.navbar > .active {
  color: var(--accent-dark);
}
  
@media screen and (max-width: 688px) {
  /* .navbar-background {
    padding: 5px 0;
  } */
}
.footer-background {
  width: 100%;
  background-color: var(--accent-light);
  border-radius: 20px 20px 0 0;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
}

@media screen and (max-width: 688px) {

  .footer-container {
    display: block;
    padding: 25px 0;
  }

  .footer-right {
    padding-top: 30px;
  }
}
/* Social Icons */
.social-icon-link {
    color:var(--black);
    font-size: 32px;
}

.social-media {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 150px;
    padding-top: 10px;
}

@media screen and (max-width: 688px) {

    .social-media {
        width: 130px;
    }
}
* {
  box-sizing: content-box;
  margin: 0px;
}

:root {
  /* define variables */
  --accent-light: #CBC6ED; /* #c51d2e or #ea9554 */
  --accent-dark: #612fcc;
  --white: #FFFEFE;
  --white-dark: #F8F8F8;
  --gray: #959595;
  --black: #221D30;
  --title: "Inter";
  --title-fallback: sans-serif;
  --title-resume: "Helvetica";
  --body: "Inter";
  --body-fallback: serif;
}

body {
  background-color: #FFFEFE;
  background-color: var(--white);
  max-width: 1500px;
  margin: auto;
  /* padding: 0px 75px; */
}

/* add side margins with this div */
.body {
  margin: 0px 75px;
}

/* customize appearance of text */
h1 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 32px;
  font-weight: bold;
}

h2 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 24px;
  font-weight: 500;
}

h3 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 18px;
  font-weight: bold;
}

h4 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 16px;
  padding-left: 0px;
  color: #959595;
  color: var(--gray);
  font-weight: 400;
}

h5 {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 16px;
  font-weight: 400;
}

p {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 12px;
}

a {
  color: #221D30;
  color: var(--black);
}

a:hover {
  color: #612fcc;
  color: var(--accent-dark);
}

.tag, button {
  font-family: "Inter", sans-serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 12px;
  color: #221D30;
  color: var(--black);
  background-color: #CBC6ED;
  background-color: var(--accent-light);
  padding: 10px;
}

.tag {
  border-radius: 5px;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
  display: inline-block;
}

button {
  border-radius: 20px;
  border: none;
}

button:hover {
  background-color: #612fcc;
  background-color: var(--accent-dark);
  color: #FFFEFE;
  color: var(--white);
}

.divider {
  padding: 10px;
}

.divider-big {
  padding: 40px;
}

.unbold {
  font-weight: 400;
  display: inline-block;
}

/* two column structure */
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  align-items: center;
  grid-gap: 10px 50px;
  gap: 10px 50px;
  justify-content: space-between;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  min-width: 400px;
}

.column > img {
  width: 100%; 
  min-width: 400px;
  height: auto; 
  background-color: #F8F8F8; 
  background-color: var(--white-dark);
  border-radius: 5px;
}

.column > * {
  margin: 10px 0;
}

.col-big {
  flex: 2 1;
  min-width: 400px;
}

.col-small {
  min-width: 300px;
}

/* image above text when wrapped */
.reverse {
  flex-wrap: wrap-reverse;
}


@media screen and (max-width: 688px) {

  .body {
    margin: 0px 20px;
  }

  h1 {
    font-size: 24px;
  }
  
  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 16px;
  }

  h4, h5 {
    font-size: 14px;
  }

  p, .tag {
    font-size: 10px;
  }

  .column, .column > img {
    min-width: 100%;
    max-width: 100%;
  }
}
#dev-body {
    background-color: var(--black);
    color: var(--white);
    margin: -20px 0;
    padding: 20px 0;
    z-index: -1;
}

#dev-body .tag {
    background-color: var(--accent-dark);
    color: var(--white);
}
.card-container {
    padding: 25px;
    position: relative;
}

.card-container-2 {
    padding: 15px;
}

.card-container:hover {
    color: var(--black);
    background-color: var(--accent-light);
    border-radius: 0 10px 10px 0;
    box-shadow: -150px 0 var(--accent-light);
    transition: box-shadow 750ms, background-color 750ms;
}

.card-container:hover .tag {
    color: var(--black);
    background-color: var(--white);
}

.card-link {
    text-decoration: none;
    color: var(--black);
}

/* surprise gif */
.card-gif {
    display: none;
}

.card-container:hover .card-gif {
    display: block;
    position: absolute;
    right: 10px;
    bottom: 0;
    max-width: 200px;
    height: auto;
}

.featured-tags {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    flex-wrap: wrap;
}

@media screen and (min-width: 1050px) {
    /* align text to the right when image is on the right */
    .align-right {
        margin-left: auto; 
        margin-right: 0;
        text-align: right;
    }
    .align-right > .featured-tags {
        justify-content: flex-end;
    }
}

@media screen and (max-width: 688px) {
    .card-container {
        padding: 10px 0px;
        position: relative;
    }
}
.purple-tag {
    background: var(--accent-lighter);
    padding: 5px 10px;
    border-radius: 5px;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    margin: 0px 15px 10px 0;
    display: inline-block;
    font-family: var(--body), var(--body-fallback);
    font-size: 10px;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.popup-inner {
    position: relative;
    padding: 30px;
    height: 80%;
    background-color: var(--white-dark);
    border-radius: 20px;
}

.popup-inner video {
    max-height: 100%;
}

.close-btn {
    position: absolute;
    width: 32px;
    height: 32px;
    border: 1px solid var(--accent-dark);
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    color: var(--black);
    top: 16px;
    right: 16px;
    z-index: 9999;
}

.close-btn:hover {
    background-color: var(--accent-dark);
    color: var(--white);
}
/* home styling */
#home-background {
    width: 100%;
    background-color: var(--accent-light);
    border-radius: 0 0 20px 20px;
    position: relative; 
    top: -75px;
}

.home-gif {
    top: 75px;
    max-width: 300px;
    height: auto;
    position: absolute;
}

.page-body {
    padding: 50px 0;
    padding-top: 75px;
}

/* resume page styling */
.right-align {
    float: right;
    z-index: 1;
} 

.clear-float {
    clear: both;
}

/* about paage styling */
.about-doodle {
    background-color: var(--white) !important;
    min-width: 70% !important;
    max-width: 150px;
    margin: auto;
}
.ux-projects {
    margin: 50px 0;
}
/* AlignedSection style sheet */
/* customize appearance of text */

/* font style for Erica Fu */
.name {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
}

/* font style for capitalized titles on the left */
.capitalized {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
    text-transform: uppercase;
}

/* bolded title text */
.bolded-title {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: bold;
    padding-left: 0px;
}

/* faded title text */
.faded-title, th {
    font-family: var(--title-resume), var(--title-fallback);
    font-weight: normal;
    color: gray;
}

/* for dates, content, and location text */
.dates, .contact, td{
    font-family: var(--title-resume), var(--body-fallback);
}

.right-section>a {
    text-decoration: none;
    color: var(--black);
}

.same-line {
    display: inline;
    overflow: auto;
}

.location {
    float: right;
    font-family: var(--title-resume), var(--body-fallback);
}

ul {
    list-style-position: outside;
    margin-left: 20px;
}
.right-section li {
    font-family: var(--title-resume), var(--body-fallback);
}

table {
    width: 100%;
}
td, th {
    text-align: left;
    vertical-align: top;
}

/* mobile version: */
@media screen and (min-width: 0px) {
    /* customize appearance of text */

    /* font style for Erica Fu */
    .name {
        font-size: 22px;
    }

    /* font style for capitalized titles on the left */
    .capitalized {
        font-size: 18px;
    }

    /* bolded title text */
    .bolded-title {
        font-size: 14px;
    }

    /* faded title text */
    .faded-title, th {
        font-size: 14px;
    }

    /* for dates, contact, and location text */
    p, td, .dates, .contact{
        font-size: 12px;
    }

    .location {
        font-size: 12px;
    }

    li {
        font-size: 12px;
    }

    /* section formatting */
    .aligned-section {
        padding: 10px 0px;
    }

    .left-section {
        padding: 0px 10px;
    }
    .right-section {
        padding: 0px 10px;
    }
}

/* laptop version: */
@media screen and (min-width: 700px) {
    /* font style for Erica Fu */
    .name {
        font-size:28px;
    }

    /* font style for capitalized titles on the left */
    .capitalized {
        font-size: 20px;
    }

    /* bolded title text */
    .bolded-title {
        font-size: 18px;
    }

    /* faded title text */
    .faded-title, th {
        font-size: 18px;
    }

    /* for dates, content, and location text */
    p, td, .dates, .location, li, .contact {
        font-size: 14px;
    }
    
    /* formatting for sections */
    .aligned-section {
        display: flex;
    }
    
    .left-section, .right-section {
        padding: 0px 10px;
    }
    .left-section {
        flex: 0 0 30%;
        max-width: 300px;
        text-align: right;
    }
    .right-section {
        flex: 0 0 60%;
    }
}
.intro-img {
    height: 100%;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
    background-color: var(--white-dark);
    border-radius: 5px;
}

hr {
    margin-top: 50px;
    border: 1px solid var(--accent-light);
    top: 20px;
    /* position: -webkit-sticky;
    position: sticky; */
}
.section-container {
    display: flex;
    margin: 50px 0;
    flex-wrap: wrap;
    grid-gap: 20px 50px;
    gap: 20px 50px;
}

.section-right {
    flex: 5 1;
    min-width: 500px;
}

.section-left {
    min-width: 100px;
    flex: 1 1;
}

@media screen and (max-width: 688px) {
    .section-right, .section-left {
        min-width: 100%;
        max-width: 100%;
    }
}
.subsection {
    margin-bottom: 20px;
}

.subsection-img {
    max-width: 700px;
    border-radius: 10px;
    display: block;
    margin: auto;
    margin-bottom: 20px;
}

.wide {
    max-width: 100%;
    border-radius: 10px;
    margin-bottom: 20px;
}
